html,body,#root{height:100%;height:-webkit-fill-available}html{background:url(/eberron-trains/static/media/train-bg.e2ba9cef.jpg) no-repeat center center fixed;background-size:cover}#root{box-shadow:inset 0 0 0 500px rgba(0,0,0,0.4);overflow:auto}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.station-block{width:100%;margin:10px 25px}.station-input{box-sizing:border-box;width:100%;padding:12px 20px;border:1px solid black;border-radius:4px;font-size:1.5rem;background-color:rgba(255,255,255,0.8)}@media (min-width: 480px){.station-block{width:440px}}

.form{display:flex;flex-direction:column;flex-wrap:wrap;justify-content:center;align-items:center;width:auto;box-sizing:border-box;grid-area:form}.form div{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center}.form button{max-width:400px;width:100%;height:50px}span{font-size:2rem;color:white}button{width:120px;margin:0 5px;height:40px;font-size:1.5rem;border:1px solid black;border-radius:4px;background-color:rgba(255,255,255,0.8)}button:hover{background-color:rgba(205,205,205,0.8)}button:active{background-color:rgba(155,155,155,0.8)}@media (min-width: 980px){.form button{width:100%;max-width:930px}}

.answer{grid-area:answer;display:grid;grid-template-columns:1fr;grid-template-rows:0.4fr 1.6fr;grid-gap:1px 1px;gap:1px 1px;grid-template-areas:"answer-distance" "answer-time" "answer-cost" "answer-path"}.answer-distance{grid-area:answer-distance}.answer-time{grid-area:answer-time}.answer-cost{grid-area:answer-cost}.answer-path{grid-area:answer-path}.answer-block{max-width:100%;padding:12px 20px;margin:10px 25px;border:1px solid black;border-radius:4px;font-size:1.5rem;background-color:rgba(255,255,255,0.8)}@media (min-width: 980px){.answer-block{max-width:400px}.answer{grid-template-columns:1fr 1fr;grid-template-areas:"answer-distance answer-time" "answer-cost answer-path"}}span{color:black;font-size:1.5rem}span.answer-cost-bold{font-weight:bold}span.answer-category-italic{font-style:italic}ul{margin:0;padding-left:20px}

.settings{width:100%;padding:12px 20px;margin:10px 25px;border:1px solid black;border-radius:4px;font-size:1.5rem;background-color:rgba(255,255,255,0.8);grid-area:settings;box-sizing:border-box;display:flex;flex-direction:row;justify-content:space-around;flex-wrap:wrap}.settings-block{display:flex;flex-direction:column;align-items:center}.settings-block select,.settings-block input{margin:5px;width:100%;font-size:1rem}.settings-block input{width:50px}.settings-custom{max-width:500px}@media (min-width: 870px){.settings{width:930px}}

main{padding-top:50px;display:flex;flex-direction:column;align-items:center;justify-items:center}.error-message{color:red;font-size:2rem;text-shadow:black 1px 0 10px;text-align:center}

